import React, { useRef, useState, useEffect } from 'react';
import './App.css';
import { useRive, useStateMachineInput } from "@rive-app/react-canvas";
import womensDayRive from './womens-day.riv';
import { Slider, TextField, Typography, Button, createTheme, ThemeProvider, Box, List, ListItem, ListItemText, Link } from '@mui/material';

// Erstellen Sie ein benutzerdefiniertes Thema mit runden Buttons
const theme = createTheme({
  palette: {
    primary: {
      main: '#000000', // Schwarz als primre Farbe
    },
  },
  components: {
    MuiButton: {
      styleOverrides: {
        root: {
          borderRadius: '50px', // Stellt einen hohen Wert fr stark gerundete Ecken ein
        },
      },
    },
  },
});

function App() {
  const canvasRef = useRef(null);
  const { rive, RiveComponent } = useRive({
    src: womensDayRive,
    autoplay: true,
    stateMachines: "State Machine",
  });

  const maleInput = useStateMachineInput(rive, "State Machine", "Male");
  const femaleInput = useStateMachineInput(rive, "State Machine", "Female");

  const [maleValue, setMaleValue] = useState(66);
  const [femaleValue, setFemaleValue] = useState(34);
  const [headlineText, setHeadlineText] = useState("Woman at german universities with habilitation");
  const [sourceText, setSourceText] = useState("www.bmbf.de, www.destatis.de");

  useEffect(() => {
    if (maleInput && femaleInput) {
      maleInput.value = maleValue;
      femaleInput.value = femaleValue;
    }

    if (rive) {
      rive.setTextRunValue("malePercentage", `${maleValue}`);
      rive.setTextRunValue("femalePercentage", `${femaleValue}`);
      rive.setTextRunValue("womensDayHeadline", headlineText);
      rive.setTextRunValue("Datasource", sourceText);
    }
  }, [rive, maleValue, femaleValue, headlineText, sourceText]);

  const handleSliderChange = (gender, event, newValue) => {
    if (gender === 'male') {
      setMaleValue(newValue);
      setFemaleValue(100 - newValue);
    } else if (gender === 'female') {
      setFemaleValue(newValue);
      setMaleValue(100 - newValue);
    }
  };

  const handleHeadlineChange = (event) => {
    setHeadlineText(event.target.value);
  };

  const handleSourceChange = (event) => {
    setSourceText(event.target.value);
  };

  const exportCanvasAsPNG = () => {
    const canvas = canvasRef.current.querySelector('canvas');
    if (!canvas) {
      console.error('Canvas-Element wurde nicht gefunden.');
      return;
    }
    const imageURL = canvas.toDataURL('image/png');
    const link = document.createElement('a');
    link.href = imageURL;
    link.download = 'rive-animation.png';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    <ThemeProvider theme={theme}>
      <div className="App">
        <Box className="App-canvas-container" ref={canvasRef} sx={{ mb: 4 }}>
          <RiveComponent style={{ width: '100%', height: '100%' }} />
        </Box>
        <Box className="App-inputs-container" sx={{ mx: 4 }}>
          <Box sx={{ mb: 4 }}>
            <Typography variant="h4"><strong>Equal?</strong></Typography>
            <br/>
            <Box sx={{ mb: 1 }}>
              <Typography>Aiming for equal rights and opportunities, it is important to be informed about the current situation and to share this information. Our tool allows you to easily create your own graphic on gender equality and spread the message.</Typography>
            </Box>
            <Typography variant="caption" sx={{ lineHeight: '0' }}>
              <strong>*The tool is designed to offer a way to visualize research data and communicate inequalities between genders. In current research data, intersexuality is usually neglected. We see a need for action here.</strong>
            </Typography>
          </Box>
          <Box sx={{ mb: 3 }}>
            <TextField
              label="Write Headline"
              variant="outlined"
              value={headlineText}
              onChange={handleHeadlineChange}
              fullWidth
              margin="normal"
            />
          </Box>
          <Box sx={{ mb: 3 }}>
            <Typography>Male in <strong>{maleValue}%</strong></Typography>
            <Slider 
              value={maleValue} 
              onChange={(e, newValue) => handleSliderChange('male', e, newValue)} 
              aria-labelledby="input-slider"
              min={0} 
              max={100}
            />
          </Box>
          <Box sx={{ mb: 4 }}>
            <Typography>Female in <strong>{femaleValue}%</strong></Typography>
            <Slider 
              value={femaleValue} 
              onChange={(e, newValue) => handleSliderChange('female', e, newValue)} 
              aria-labelledby="input-slider"
              min={0} 
              max={100}
            />
          </Box>
          <Box sx={{ mb: 3 }}>
            <TextField
              label="Write Source"
              variant="outlined"
              value={sourceText}
              onChange={handleSourceChange}
              fullWidth
              margin="normal"
            />
          </Box>
          <Box sx={{ mb: 6 }}>
            <Button onClick={exportCanvasAsPNG} variant="contained" color="primary">Download</Button>
          </Box>
          <Box sx={{ mb: 1 }}>
            <Typography variant="h6"><strong>Sources</strong></Typography>
          </Box>
          <Box sx={{ mb: 4 }}>
            <List>
              <ListItem 
                component="a" 
                href="https://eige.europa.eu/gender-equality-index/2023/DE" 
                sx={{ 
                  py: 0.1, 
                  textDecoration: 'none', // Entfernt die Unterstreichung des Links
                  color: '#52547C', // Setzt die Farbe des Links
                  '&:hover': {
                    color: 'darken(#52547C, 20%)',
                  },
                }}>
                <ListItemText primary="EIGE" />
              </ListItem>
              <ListItem 
                component="a" 
                href="https://www.oecd.org/gender/data/" 
                sx={{ 
                  py: 0.1, 
                  textDecoration: 'none', 
                  color: '#52547C', 
                  '&:hover': {
                    color: 'darken(#52547C, 20%)',
                  },
                }}>
                <ListItemText primary="OECD Gender Data" />
              </ListItem>
              <ListItem 
                component="a" 
                href="https://genderdata.worldbank.org/" 
                sx={{ 
                  py: 0.1, 
                  textDecoration: 'none', 
                  color: '#52547C', 
                  '&:hover': {
                    color: 'darken(#52547C, 20%)',
                  },
                }}>
                <ListItemText primary="Worldbank" />
              </ListItem>
              <ListItem 
                component="a" 
                href="https://ilostat.ilo.org/topics/women/" 
                sx={{ 
                  py: 0.1, 
                  textDecoration: 'none', 
                  color: '#52547C', 
                  '&:hover': {
                    color: 'darken(#52547C, 20%)',
                  },
                }}>
                <ListItemText primary="ILOSTAT" />
              </ListItem>
            </List>
          </Box>
        </Box>
      </div>
    </ThemeProvider>
  );
}

export default App;
